class Tape extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: 3,
      tooltip: true

    }
  }
  componentDidMount() {
    if (localStorage.getItem("Tool") === null) {
      this.setState({ tooltip: true })
    } else this.setState({ tooltip: false })
    if (localStorage.getItem('grow') != "1") {
      connect.send("VKWebAppJoinGroup", { "group_id": 176434730 });
      localStorage.setItem('grow', "1")
    }
  }



  drawThisImage(object) {
    this.props.app.setState({
      selectedImage: object.photo.src,
      activeStory: 'create',
      photoObject: object
    })
  }

  render() {
    return (
      <Panel id={this.props.id}>
        <PanelHeader>Шаблоны</PanelHeader>

        <Div>
          <FormLayout>
            <a href="https://vk.com/public176434730" target="_blank">ОТКРЫТЬ ЛЕНТУ МЕМОВ</a>
            <a href="https://vk.com/write-189160722" target="_blank">РАЗЫГРАТЬ ДРУГА!</a>
            <FormLayoutGroup top="КОМИКСЫ">
              <Tooltip
                text="Привет! Это шаблоны для ваших будущих мемов. Кликай по нужному, включай фантазию и начинай творить! "
                isShown={this.state.tooltip}
                onClose={() => {
                  this.setState({ tooltip: false })
                  localStorage.setItem("Tool", 1)

                }}
                offsetX={20}
              >
                <div className="hint">

                  <Gallery photos={PHOTO} onClick={(index, object) => { this.drawThisImage(object) }} columns={this.state.columns} />
                </div>
              </Tooltip>
            </FormLayoutGroup>
            <FormLayoutGroup top="ПЕРСОНАЖИ">
              <Gallery photos={PHOTO_SET} onClick={(index, object) => { this.drawThisImage(object) }} columns={this.state.columns} />
            </FormLayoutGroup>
          </FormLayout>
        </Div>

      </Panel>
    )
  }
}
export default Tape;



